let changelook= ['所有人可见', '仅自己可见'];
import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue";
// 解决bug:
import uniIcons from "../../components/uni-icon/uni-icons.vue";
import uploadImages from "../../components/common/upload-images.vue";
import uniPopup from "../../components/uni-popup/uni-popup.vue";
export default {
	name:'AddInput',
	components:{
		uniNavBar,
		uploadImages,
		uniPopup,
		uniIcons
		
	},
	data() {
		return {
			showpopup:true,
			yinsi:"所有人可见",
			text:"",
			imglist:[],
			isget:false,// 是否保存草稿
		};
	},
	// 监听页面返回事件
	onBackPress(){
		//如果有输入内容
		if(!this.text&&this.imglist.length>1){return;}
		if(!this.isget){
			this.baocun();
			return true;
		}
	},
	methods:{
		// 返回
		back(){
			uni.navigateBack({ delta: 1 });
		},
		// 发布
		submit(){
			console.log("发布")
		},
		// 隐私
		changelook(){
			uni.showActionSheet({
				itemList:changelook,
				success: (res)=> {
					this.yinsi=changelook[res.tapIndex]
				}
			});
		},
		uploud(arr){
			this.imglist=arr;
			console.log(this.imglist)
		},
		hidePopup(){
			this.showpopup=false;
		},
		// 保存草稿
		baocun(){
			uni.showModal({
				content:'是否保存为草稿',
				cancelText:'不保存',
				confirmText:'保存',
				success:res=>{
					if(res.confirm){
						console.log('保存')
					}else{
						console.log('不保存')
					}
					this.isget=true;
					uni.navigateBack({
						delta:1
					})
				}	
			})
		}
		
	}
}
